html {
  --active-nav-color: #00acdc;
}

body {
  margin: 0;
  font: 500 12px / 14px Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.img-wrapper {
  width: 40px;
  height: 40px;
}

.img {
  width: 100%;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-item:not(:last-child) {
  margin-right: 20px;
}

.nav-link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav-link:hover, .is-active {
  color: var(--active-nav-color);
}

.is-active:after {
  content: "";
  background-color: var(--active-nav-color);
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

